        // }
        
        // function initLocalSettings(id, activeClass) {
        //     const elements = document.getElementById(id);
    
        //     elements.forEach(elem => {
        //         elem.classList.remove(activeClass);
        //         if (elem.getAttribute('id') === localStorage.getItem('chosen')) {
